import canvas from './Canvas'

var inputMode = {
    mode: 4,

    sideMenu: 4,
    selectionGroupVal: 1,

    selectionGroup: 0,
    selection: 1,
    move: 2,
    draw: 3,
    smartDraw: 4,
    text: 5,
    shapes: 6,
    erase: 7,
    eraseAll: 8,
    laser: 9
}

inputMode.inDrawMode = function() {
    return inputMode.mode == inputMode.draw || inputMode.mode == inputMode.smartDraw;
}

inputMode.selectSideMenu = function(menu) {
    var tpanel = document.getElementById("transparentPanel");
    if (menu != inputMode.eraseAll) {
        if (menu == inputMode.selectionGroup) {
            menu = inputMode.selectionGroupVal;
        }
        if (menu == inputMode.selection || menu == inputMode.move) {
            inputMode.sideMenu = inputMode.selectionGroup;
            inputMode.selectionGroupVal = menu;
        } else {
            inputMode.sideMenu = menu;
        }
        inputMode.mode = menu;
    }
    if (menu == inputMode.selection) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.SELECTION);
        tpanel.style.cursor = 'auto';
    } else if (menu == inputMode.move) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.SELECTION);
        tpanel.style.cursor = 'all-scroll';
    } else if (menu == inputMode.laser) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.POINTING);
        tpanel.style.cursor = 'url("/red_circle.svg") 10 10, auto';
    } else if (menu == inputMode.draw) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.DRAWING);
        window.Module.enableRecognition(false);
        tpanel.style.cursor = 'url("/pen-solid.svg") 0 20, auto';
        //tpanel.style.cursor = 'auto';
    } else if (menu == inputMode.smartDraw) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.DRAWING);
        window.Module.enableRecognition(true);
        //tpanel.style.cursor = 'url("/pen-solid.svg") 0 20, auto';
        tpanel.style.cursor = 'auto';
    } else if (menu == inputMode.text) {
        // text mode
        window.Module.inputReceiver().setMode(window.Module.InputMode.SELECTION);
        tpanel.style.cursor = 'url("/title_black.svg") 0 20, auto';
    } else if (menu == inputMode.shapes) {
        // shapes
        window.Module.inputReceiver().setMode(window.Module.InputMode.SELECTION);
        tpanel.style.cursor = 'auto';
    } else if (menu == inputMode.erase) {
        // erase
        window.Module.inputReceiver().setMode(window.Module.InputMode.ERASING);
        tpanel.style.cursor = 'url("/eraser-solid.svg"), auto';
    } else if (menu == inputMode.eraseAll) {
        // erase all
        window.Module.eraseAll();
        canvas.draw();
    }
    return inputMode.sideMenu;
}

export default inputMode;
