import canvas from './Canvas';
import command from './Command'

var keyboardInput = { 
    showingDialog: false
};

const isMac = navigator.userAgent.indexOf("OS X") > 0;

var handlers = {
    'a': command.selectAll,
    'c': command.copy,
    'x': command.cut,
    'v': command.paste,
    'b': command.addBox,
    'e': command.addCircle,
    'u': command.addUser,
    'z': command.undo,
}

keyboardInput.handleKeyDown = function(event) {
    if (keyboardInput.showingDialog) {
        // do not handle keyboard while showing a dialog
        return;
    }

    let key = event.key.toLowerCase();
    var handled = false;
    if (key == 'backspace' || key == 'delete') {
        command.del();
        handled = true;
    } else if (key == 'escape') {
        command.escape();
        handled = true;
    } else {
        var handler = handlers[key];
        if (!event.shiftKey && !event.altKey) {
            if (handler && ((isMac && event.metaKey) || (!isMac && event.ctrlKey))) {
                handler();
                handled = true;
            }
        }
        if (isMac && key == 'z' && event.shiftKey && event.metaKey) {
            command.redo();
            handled = true;
        }
        if (!isMac && key == 'z' && event.shiftKey && event.ctrlKey) {
            command.redo();
            handled = true;
        }
        if (!isMac && key == 'y' && event.ctrlKey && !event.shiftKey && !event.altKey) {
            command.redo();
            handled = true;
        }
    }
    if (handled) {
        event.preventDefault();
        canvas.draw();
    }
}

keyboardInput.attachEvents = function(canvasElement) {
    document.addEventListener("keydown", function(event) {
        keyboardInput.handleKeyDown(event);
    });
}

export default keyboardInput;
